"use client";

import { Button, Stack, Typography } from "@mui/material";
import { useState } from "react";

export default function Skills() {
  const [selectedSkill, setSelectedSkill] = useState("All");

  return (
    <Stack spacing={3} alignItems={"center"} width={"100%"}>
      <Typography variant="h4" fontWeight={"bold"}>
        My Skills
      </Typography>
      <Stack direction={"row"} spacing={2} width={"90%"} alignItems={"center"}>
        {["All", "Frontend", "Backend", "DevOps"].map((button) => (
          <Button
            key={button}
            sx={{
              color: selectedSkill === button ? "primary.main" : "text.primary",
              borderColor:
                selectedSkill === button ? "primary.main" : "grey.400",
              backgroundColor:
                selectedSkill === button ? "primary.main" : "white",
            }}
            variant="outlined"
            onClick={() => setSelectedSkill(button)}
          >
            {button}
          </Button>
        ))}
      </Stack>
    </Stack>
  );
}
